.slider-input {
  height: 30px !important;
}

.slider-input .rangeslider__handle {
  background: #ffc107 !important;
  border: 1px solid #ffc107 !important;
  border-radius: 4px !important;
  height: 36px !important;
  width: 36px !important;
}

.rangeslider-horizontal .rangeslider__handle:after {
  background-color: transparent !important;
  box-shadow: none !important;
}

.rangeslider-horizontal .rangeslider__fill {
  background-color: #fbbe09bd !important;
}

.rangeslider,
.rangeslider .rangeslider__fill {
  display: block !important;
  box-shadow: inset 0 1px 3px #fbbe096b !important;
}

.rangeslider {
  background: #fff7de !important;
  margin-top: 8px !important;
  margin-bottom: 4px !important;
}

.input-box {
  border: 1px solid #fbbe09bd !important;
  border-radius: 6px !important;
  height: 40px !important;
  margin: 0 !important;
  outline: 0 none !important;
  margin-left: 4px !important;
  margin-right: 4px !important;
  width: 100px;
}

.input-box-div .input-col {
  text-align: end;
}

.hr-input {
  border-top: 3px solid #ffbf00bd;
}

.input-box-bottom-right {
  text-align: end;
}

.checkbox-button {
  background: #fbbe0979;
  border: 1px solid #fbbe0979;
  color: rgb(0, 0, 0);
  text-align: center;
  padding: 8px;
  cursor: pointer;
  border-radius: 4px;
}
.checkbox-button p {
  margin: 0px;
}

.checkbox-button:hover {
  background: #fbbe09;
}

.checkbox-button-active {
  background: #fbbe09;
  border: 1px solid #fbbe09;
}

.sub-input-text {
  font-size: 15px;
  color: gray;
}

.result-div ul {
  border: 1px solid #d5e3e8;
  display: table;
  margin: 0;
  padding: 0;
  width: 100%;
}

.result-div ul li:first-child {
  border-left: 0 none;
}

.result-div ul li {
  font-weight: 400;
  font-style: normal;
  -moz-font-feature-settings: "calt=0,liga=0";
  font-feature-settings: "liga" 0;
  border-left: 1px solid #d5e3e8;
  border-bottom: 1px solid #d5e3e8;
  font-size: 0.9rem;
  list-style-type: none;
  margin: 0;
  padding: 10px 6px;
}

.result-div {
  margin: 0 0 20px;
  text-align: center;
}

.result-div ul li strong {
  font-weight: 700;
  font-style: normal;
  letter-spacing: 0.015em;
  -moz-font-feature-settings: "calt=0,liga=0";
  font-feature-settings: "liga" 0;
  display: block;
  font-size: 14px;
  text-transform: uppercase;
}

.tool-btn-div ul {
  border: 1px solid #d5e3e8;
  display: table;
  margin: 0;
  padding: 0;
  width: 100%;
}

.tool-btn-div ul li:first-child {
  border-left: 0 none;
}

.tool-btn-div ul li {
  font-weight: 400;
  font-style: normal;
  -moz-font-feature-settings: "calt=0,liga=0";
  font-feature-settings: "liga" 0;
  border-left: 1px solid #d5e3e8;
  display: table-cell;
  font-size: 0.9rem;
  list-style-type: none;
  margin: 0;
  padding: 10px 6px;
  width: 33.332%;
  cursor: pointer;
}

.tool-btn-div ul li:hover {
  background: #d5e3e8;
}

.tool-btn-active {
  background: #d5e3e8;
}

.tool-btn-div ul li a {
  text-decoration: none;
  color: black;
}

.tool-btn-div {
  margin: 0 0 20px;
  text-align: center;
}

.tool-btn-div ul li strong {
  font-weight: 700;
  font-style: normal;
  letter-spacing: 0.015em;
  -moz-font-feature-settings: "calt=0,liga=0";
  font-feature-settings: "liga" 0;
  display: block;
  font-size: 14px;
  text-transform: uppercase;
}
